@charset "UTF-8";
@import "../../../style/theme/default/var.css";
.template-page {
  padding: var(--tab-padding-middle);
}

@component-namespace constantly{
  @b grid {
    /*margin-left: -var(--theme-margin-middle);*/
    .el-col{
      margin-bottom: var(--theme-margin-base);
      &.el-col-12 {
        margin-bottom: 0;
      }
    }
    .customer-title {
      margin: 0;
      font-size: 14px;
      font-weight: bold;
    }
    .customer-echart {
      height: 300px;
    }
    @e content{
      border: 1px solid var(--border-color-base);
      background-color: var(--color-white);
      .total-num {
        padding: var(--theme-padding-base) 0;
        margin: 0;
        text-align: center;
        background-color: var(--theme-background-color-primary);
      }
    }
  }
  @b widget{
    &.xe-widget.xe-counter {
      margin-bottom: 0;
     }

    &.xe-widget.xe-counter .xe-icon {
       padding: 25px;
       background-color: var(--theme-color-primary);

      & i {
          font-size:44px;
          background-color: transparent;
      }
    }

   &.xe-widget.xe-counter .xe-icon + .xe-label {
     padding-left: var(--theme-padding-middle);
    }
    &.xe-widget.xe-counter .xe-label{
      &  .num ,&  .normal-span{
         font-weight: bold;
        color: var(--theme-font-color-primary);
       }
      &  .num  b{
         font-size: var(--theme-font-size-middle);
       }
      & .cut{
          b{
            font-weight: normal;
            padding-left: 4px;
            padding-right: 4px;
            border-left:1px solid var(--theme-font-color-secondary);
            cursor: pointer;
          @when active{
            font-weight:bold;
          }
          &:first-child {
             border-left:0;
             padding-left:0;
           }
          &:last-child {
             padding-right:0;
           }
        }
      }
      span {
       margin-top: var(--theme-margin-small);
       font-size: var(--theme-font-size-small);
       &.normal-span {
          display: inline-block;
          font-size: inherit;
        }
      }
      i {
       cursor: pointer;
       color: var(--theme-background-color-secondary);
      }
      & .constantly-widget-tips{
       display: inline-block;
       margin-top:0;
      }
    }

    @when unfill{
      &.xe-widget.xe-counter {
        margin-bottom:0;
      }
      &.xe-widget.xe-counter .xe-icon {
        padding: 10px;
        background-color: transparent;
      & i {
            color: var(--theme-color-primary);
            width: 68px;
            height: 68px;
            line-height: 64px;
            border: 2px solid var(--theme-color-primary);
       }
      }
      &.xe-widget.xe-counter .xe-icon + .xe-label {
         padding-left: 0;
       }
    }
  }

 @b panel {
   padding: 0 var(--theme-padding-base) var(--theme-padding-large);
   background-color: var(--color-white);
   border: 1px solid var(--border-color-base);
   margin-bottom: var(--theme-margin-base);
   &:last-child {
    margin-bottom: 0;
   }
   .el-form-item__label {
     padding: 9px 0;
   }
   @e title{
     height:50px;
     line-height: 30px;
     padding: var(--theme-padding-base) 0;
     & > span{
       position: relative;
       display: inline-block;
       padding:0 var(--theme-padding-small);
       &:before {
        content: ' ';
        position: absolute;
        top: -10px;
        left: 5px;
        right: 5px;
        height: 4px;
        background-color: var(--theme-color-primary);
      }
     }
    .el-form-item__label {
       float: left;
     }
    .el-form-item {
      margin-right: 0;
    }
   }
 }
 @b form {
   @e platform {
     @utils-clearfix;
     & .el-badge {
         margin-right:var(--theme-margin-small);
         float:left;
       }

     & .el-badge > span{
         display: inline-block;
         text-align: center;
         cursor: pointer;
         width:30px;
         height:30px;
         border-radius: 100%;
         font-size: var(--theme-font-size-small);
         color: var(--color-white);
         background-color: #999999;
       @when active{
         background-color: var(--theme-color-primary);
       }
     }
     .el-badge__content{
       z-index: 1;
       padding:0 4px;

     }
   }
    .spacing-button{
      line-height: 20px;
      display:inline-block;
      .el-radio-button {
        .el-radio-button__inner {
          padding: 8px 20px;
          color: var(--font-color-base);
          border: 1px solid var(--theme-border-color-primary);
          background-color: transparent;
        }
      }
      .el-radio-button__orig-radio:checked + .el-radio-button__inner {
        color: var(--color-white);
        border-color: var(--theme-color-primary);
        background-color: var(--theme-color-primary);
      }
    }
 }
 @b echarts {
   .echarts {
     width: 100%;
     height: 350px;
   }
 }
}
/* 营销活动 */

@component-namespace activity{
  @b calendar {
    padding-bottom: var(--theme-padding-base);
    .constantly-panel__body {
      max-width: 750px;
      margin: 0 auto;
    }
    .el-calendar__header {
      display: none;
    }
    .el-calendar-table {
      tbody tr:first-child {
        display: none;
      }
    }
    .el-calendar-table td.current:not(.disabled) .el-calendar__date, .el-calendar-table td.end-date .el-calendar__date, .el-calendar-table td.start-date .el-calendar__date {
      background-color: transparent!important;
    }
    .el-calendar .el-calendar-table td.today:after, .el-calendar .el-calendar-table td.today:before, .el-calendar .el-calendar-table td.today {
      border-color: #d74548!important;
    }
    .el-calendar-table td.today {
      background-color: #d74548!important;
    }
    .el-calendar__date .date-almanac {
      color: var(--color-white);
    }
    .el-calendar-table td.current:not(.disabled) .el-calendar__date-value, .el-calendar-table td.end-date .el-calendar__date-value, .el-calendar-table td.start-date .el-calendar__date-value, .el-calendar-table td.today .el-calendar__date-value {
      color: var(--color-white);
    }
    .el-calendar__date {
      position: relative;
      height: 50px;
      padding: var(--theme-padding-small);
      .el-calendar__date-value {
        position: absolute;
        top:0;
        left:0;
        right:0;
        padding: var(--theme-padding-small);
        z-index: 2;
      }
      .name-day {
        position: absolute;
        top: 1px;
        left: 1px;
        right:0;
        padding: var(--theme-padding-small);
        text-align: left;
        color: var(--color-white);
        z-index: 3;
      }
      .activity-list__status {
        position: absolute;
        top: -1px;
        left:-1px;
        right:-1px;
        bottom: -1px;
        padding-top: 25px;
        background-color: var(--theme-color-primary);
        .name {
          color: var(--color-white);
        }
      }
    }
    .el-calendar__date-content, .el-calendar__date-scroll {
      height: 20px;
    }
  }
  @b content {
    padding-bottom: 0;
    .constantly-panel__body {
      min-height: 317px;
    }
    /* 客户关怀模块 */
    .list-area {
      display: block;
      width: 25%;
      float: left;
      padding: 0 0 43px;
      position:relative;
      span {
        color: var(--theme-font-color-primary);
        font-size: 12px;
        display: block;
        margin: 0 auto;
        text-align: center;
        &.icon  {
           width: 40px;
           color: #7c7c7c;
           line-height: 40px;
           background: var(--theme-background-color-primary);
           border-radius: 100%;
           font-size: 20px;
           margin-bottom: 5px;
         }
      }
      &.chosed-unit{
         position: relative;
        span {
          color: var(--theme-color-primary);
          &.icon {
             background: var(--theme-color-primary);
             color: var(--color-white);
           }
        }
      }
      label {
        display: block;
        position: absolute;
        top: -5px;
        left: 54%;
        width: 15px;
        height: 15px;
        font-size: 12px;
        color: var(--color-white);
        border-radius: 50%;
        line-height: 15px;
        background: #d74548;
        text-align: center;
      }
    }
  }
  @b list{
    /* 弹出浮动 */
    @e popover{
      &.el-popover{
        padding:0;
        border:0;
      }
    }
    @e popover-header{
      padding: var(--theme-padding-base);
      background: var(--theme-color-primary);
      line-height: 20px;
      font-size:var(--theme-font-size-small);
      color: var(--color-white);
      & i{
        float:right;
        font-size: var(--theme-font-size-middle);
        cursor: pointer;
      }
    }
    /* /end 弹出浮动 */

    /* popover里面的折叠面板 */
    @e collapse{
      & .el-collapse-item__header{
        font-size: var(--theme-font-size-small);
        height: 36px;
        line-height: 36px;
        padding: 0 var(--theme-padding-base);
      &  b{
           font-weight: normal;
           color: var(--theme-color-primary);
           margin-right: var(--theme-margin-base);
         }
      }
      & .el-collapse-item__header__arrow:before{
        line-height: 36px;
      }
      & .el-collapse-item__content{
        padding: var(--theme-padding-base);
      }
    }
    /*/end  popover里面的折叠面板 */
    /* 圆角 数字标签 */
    @e badge{
      position: relative;
      height: 15px;
      & >span{
         position: absolute;
         top:-15px;
         left: 50%;
         transform: translate(-50%, 0);
         -webkit-transform: translate(-50%, 0);
         -moz-transform: translate(-50%, 0);
         background-color: var(--theme-color-primary);
         color: var(--color-white);
         border-radius: 100%;
         font-size: var(--theme-font-size-middle);
         display: inline-block;
         width: 32px;
         height: 32px;
         text-align: center;
         line-height: 32px;
        & b{
          font-size: 12px;
          font-weight: normal;
        }
      }
    }
/* /end  圆角 数字标签 */
/* /  活动名称列表 */
    @e article{
      display: block;
      @utils-ellipsis;
      height:20px;
      font-size: var(--theme-font-size-small);
      color: var(--theme-font-color-primary);
      cursor: pointer;
    }
    /* /end 活动名称列表 */
    /*  状态列表 */
    @e status{
      /*  margin-top: var(--theme-margin-middle);*/
    }
    @e status-list{
      margin-top: 2px;
      & span.item{
        float: left;
        width:100%;
        text-align: center;
        color: var(--theme-font-color-primary);
        & .name{
          display: block;
          font-size: var(--theme-font-size-small);
          color: var(--theme-font-color-primary);
        }
        & .num {
          font-weight: bold;
          font-size: 26px;
          font-size: var(--theme-font-size-base);
        }
      }
    }
  /* /end 状态列表 */
  }
}

@media (max-width: 1620px){
  .constantly-widget.xe-widget.xe-counter .xe-label span {
    font-size:12px;
  }
  .total-num {
    font-size:12px;
  }
  .constantly-widget.xe-widget.xe-counter .xe-icon + .xe-label{
    padding: var(--theme-padding-base);
  }
  .constantly-widget.xe-widget.xe-counter .xe-icon {
    padding: var(--theme-padding-middle);
  }
}
@media (max-width: 1370px) {
  .constantly-widget.xe-widget.xe-counter .xe-icon + .xe-label {
    padding: 5px;
  }

  .constantly-widget.xe-widget.xe-counter .xe-label span {
    font-size: var(--theme-font-size-small);
  }

  .total-num {
    font-size: var(--theme-font-size-small);
  }

  .constantly-widget.xe-widget.xe-counter .xe-label .num,
  .constantly-widget.xe-widget.xe-counter .xe-label .num .normal-span {
    font-size: 22px;
  }

  .constantly-widget.xe-widget.xe-counter .xe-icon {
    padding: 10px;
    & i {
      font-size: 30px;
      line-height: 44px;
      width: 44px;
      height: 44px;
    }
  }

  .constantly-widget.is-unfill.xe-widget.xe-counter .xe-icon i {
    line-height: 44px;
    width: 48px;
    height: 48px;
  }
}



